<template>
  <div class="my_address">
    <baidu-map class="baidu-map" :center="center" :zoom="zoom" :scroll-wheel-zoom="true" @ready="ready">
      <bm-map-type :map-types="mapTypes" anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-map-type>
      <bm-overview-map anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :isOpen="true"></bm-overview-map>
      <bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" @locationSuccess="locationSuccess" :showAddressBar="true"
                      :autoLocation="true" :offset="{height: 160}"></bm-geolocation>
      <bm-circle :center="center" v-bind="circlePath" :editing="false"></bm-circle>
      <bm-marker :position="center" animation="BMAP_ANIMATION_BOUNCE">
        <bm-label :content="`我的住址：${address.label}`" :labelStyle="{color: 'red', fontSize : '12px'}"
                  :offset="{width: 30, height: 15}"/>
      </bm-marker>
      <bm-transit v-if="transitEnd" :start="center" :end="transitEnd" :auto-viewport="true"
                  :location="locationCity" :panel="false"></bm-transit>
    </baidu-map>
  </div>
</template>
<script type="text/ecmascript-6">
import { address } from '@/config/global.config.js'
export default {
  name: 'MyAddress',
  data () {
    return {
      address,
      BMap: null,
      map: null,
      center: address.center,
      zoom: 12,
      circlePath: {
        radius: 100,
        strokeColor: 'blue',
        fillColor: 'lightblue',
        strokeWeight: 2,
        strokeOPacity: 0.3,
        fillOpacity: 0.2
      },
      mapTypes: ['BMAP_NORMAL_MAP', 'BMAP_PERSPECTIVE_MAP', 'BMAP_SATELLITE_MAP', 'BMAP_HYBRID_MAP'],
      transitEnd: null,
      locationCity: ''
    }
  },
  methods: {
    ready ({ BMap, map }) {
      this.BMap = BMap
      this.map = map
    },
    locationSuccess ({ point, addressComponent, marker }) {
      this.locationCity = addressComponent.city
      this.transitEnd = point
    }
  }
}
</script>
<style rel="stylesheet/css" type="text/css" lang="scss">
.my_address {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  .baidu-map {
    height: 100%;
  }

  label {
    max-width: none;
  }

  .anchorBL {
    display: none;
  }
}
</style>
